* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.mainConteiner {
  display: flex;
  flex-direction: column;
}
.conteinerWeather {
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: auto;
  font-size: 20px;
  margin: auto;
  padding: 20px;
  color: #193342;
  padding-bottom: 50px;
  padding-top: 50px;
  border-bottom: 1px solid #193342;
  border-left: 1px solid #193342;
  border-right: 1px solid #193342;
  border-radius: 0 0 10px 10px;
  background: #1933422d;
}

.containerMoreDay {
  display: flex;
  text-decoration: none;
  flex-wrap: wrap;
}
.containerOneDay {
  list-style: none;
  margin-right: 20px;
  border: 1px solid #193342;
  border-radius: 10px;
  padding: 10px;
  background: white;
  font-weight: bold;
  margin-bottom: 20px;
}

.containerToday {
  border: 1px solid #193342;
  border-radius: 10px;
  padding: 10px;
  background: white;
  font-weight: bold;
  height: 200px;
}

.flex {
  display: flex;
  font-size: 20px;
  font-weight: bold;
  width: 100%;
  color: #84abc1;
}

.currentOn {
  width: 30%;
  height: 100%;
  padding: 20px;
  border-top: 1px solid #193342;
  border-left: 1px solid #193342;
  border-right: 1px solid #193342;
  border-radius: 10px 10px 0 0;
  background: #1933422d;
}
.forecastOff {
  width: 70%;
  height: 100%;
  padding: 20px;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #193342;
}
.currentOff {
  width: 30%;
  height: 100%;
  padding: 20px;
  border-bottom: 1px solid #193342;
}

.forecastOn {
  width: 70%;
  height: 100%;
  padding: 20px;
  border-top: 1px solid #193342;
  border-left: 1px solid #193342;
  border-right: 1px solid #193342;
  border-radius: 10px 10px 0 0;
  background: #1933422d;
}
.textOn {
  color: #193342;
}

.date {
  color: #84abc1;
  font-weight: bold;
  margin-bottom: 20px;
}
.text {
  font-weight: normal;
}
.map {
  width: 50%;
  height: 200px;
  background-color: white;
}
